#root{
	height: 100% !important;
}

html {
	margin-right: 0px !important;
	height: 100%
}

body {
	margin: 0;
	padding: 0;
	height: 100%;
	border: none;
	font-family: Poppins;
	font-size: 15px;
	color: #a9a9a9;
	background: #242425;
	letter-spacing: 0;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	-webkit-text-size-adjust: 100%
}

.page {
	position: relative;
	overflow: hidden;
	height: 100%
}

@media (max-width:1023px) {
	.page {
		padding-top: 58px
	}
	.page .lines-grid {
		padding-left: 0
	}
}

* {
	box-sizing: border-box;
	-webkit-box-sizing: border-box
}

.container {
	margin: 0;
	position: relative;
	width: 100%;
	height: 100%;
	transition: all .4s ease 0s;
	-moz-transition: all .4s ease 0s;
	-webkit-transition: all .4s ease 0s;
	-o-transition: all .4s ease 0s
}

.lines-grid {
	padding-left: 44px;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	font-size: 0;
	z-index: 113;
	pointer-events: none
}

.lines-grid .row {
	margin: 0;
	height: 100%
}

.lines-grid .row .col {
	padding: 0;
	height: 100%
}

.lines-grid .row .col .lines {
	position: absolute;
	left: 0;
	top: 0;
	width: 1px;
	height: 100%
}

.lines-grid .row .col .lines .line-1,
.lines-grid .row .col .lines .line-2 {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, .05)
}

.lines-grid .row .col .lines .line-2 {
	height: 0%;
	background: rgba(255, 255, 255, .15)
}

.lines-grid .row .col:after {
	content: '';
	position: absolute;
	right: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #181818;
	transition: all .8s cubic-bezier(.77, 0, .175, 1);
	-moz-transition: all .8s cubic-bezier(.77, 0, .175, 1);
	-webkit-transition: all .8s cubic-bezier(.77, 0, .175, 1);
	-o-transition: all .8s cubic-bezier(.77, 0, .175, 1)
}

.lines-grid.loaded .row .col:after {
	width: 0
}

.lines-grid.z-index {
	z-index: 111
}

