.preloader {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	text-align: center;
	background: #181818;
	z-index: 1000
}

.preloader .spinner {
	position: absolute;
	left: 50%;
	top: 0;
	width: 1px;
	height: 100%
}

.preloader .spinner .double-bounce1,
.preloader .spinner .double-bounce2 {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, .05)
}

.preloader .spinner .double-bounce2 {
	background: rgba(255, 255, 255, .15);
	height: 0;
	-webkit-animation: loading 2s infinite ease-in-out;
	animation: loading 2s infinite ease-in-out
}

