.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 44px;
	height: 100%;
	z-index: 113;
	text-align: center;
	background: #181818
}

.header:before {
	content: '';
	position: absolute;
	right: -1px;
	top: 0;
	width: 1px;
	height: 100%;
	background: rgba(255, 255, 255, .05)
}

.header .logo {
	position: relative;
	padding: 0;
	background: #5ac24e;
	text-align: center
}

.header .logo a {
	display: block;
	height: 58px;
	line-height: 58px;
	color: #fff;
	text-decoration: none
}

.header .logo a:before {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(135deg, rgba(0, 0, 0, .5) 0, rgba(0, 0, 0, 0) 100%)
}

.header .logo a span {
	position: relative;
	font-family: 'Cormorant Garamond';
	font-size: 28px;
	font-weight: 700;
	text-transform: uppercase;
	z-index: 2
}

.header .top-menu {
	margin-top: 12px;
	width: 100%;
	font-size: 0
}

.header .top-menu ul li {
	display: block
}

.header .top-menu ul li a {
	margin: 8px 0;
	position: relative;
	display: block;
	color: #fff;
	text-align: center
}

.header .top-menu ul li a .icon {
	margin: 0;
	display: block;
	height: 32px;
	line-height: 32px;
	font-size: 30px;
	color: #a9a9a9;
	border-radius: 30px;
	-moz-border-radius: 30px;
	-webkit-border-radius: 30px;
	-khtml-border-radius: 30px;
	transition: all .3s ease 0s;
	-moz-transition: all .3s ease 0s;
	-webkit-transition: all .3s ease 0s;
	-o-transition: all .3s ease 0s
}

.header .top-menu ul li a .link {
	margin: -11px 0 0 0;
	padding: 0 8px;
	position: absolute;
	left: 120%;
	top: 50%;
	height: 22px;
	line-height: 22px;
	font-size: 12px;
	color: #fff;
	font-weight: 300;
	background: #181818;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	border-radius: 4px;
	-moz-border-radius: 4px;
	-webkit-border-radius: 4px;
	-khtml-border-radius: 4px;
	transition: all .3s ease 0s;
	-moz-transition: all .3s ease 0s;
	-webkit-transition: all .3s ease 0s;
	-o-transition: all .3s ease 0s;
	transform: translateX(-10px);
	-webkit-transform: translateX(-10px);
	-moz-transform: translateX(-10px);
	-o-transform: translateX(-10px)
}

.header .top-menu ul li a .link:before {
	margin-top: -5px;
	content: '';
	position: absolute;
	left: -10px;
	top: 50%;
	border: 5px solid transparent;
	border-right: 5px solid #111
}

@media (max-width:560px) {
	.header .top-menu ul li a .link {
		display: none
	}
}

.header .top-menu ul li:hover a {
	color: #fff
}

.header .top-menu ul li:hover a .icon {
	color: #fff
}

.header .top-menu ul li:hover a .link {
	transform: translateX(0);
	-webkit-transform: translateX(0);
	-moz-transform: translateX(0);
	-o-transform: translateX(0);
	opacity: 1;
	visibility: visible
}

.header .top-menu ul li.active a {
	color: #5ac24e
}

.header .top-menu ul li.active a .icon {
	color: #5ac24e
}

.header .social {
	position: absolute;
	left: 0;
	bottom: 10px;
	width: 100%
}

.header .social a {
	margin: 6px 0;
	display: block
}

