.theme_panel {
	position: fixed;
	right: 0;
	top: 12%;
	width: 166px;
	background: #fff;
	text-align: center;
	z-index: 999;
	opacity: 1;
	-webkit-transition: all .3s ease 0s;
	transition: all .3s ease 0s
}

.theme_panel.left {
	left: 0;
	right: auto
}

.theme_panel.left .toggle_bts {
	right: auto;
	left: 166px
}

.theme_panel.active {
	right: -166px
}

.theme_panel.left.active {
	left: -166px;
	right: auto
}

.toggle_bts {
	padding: 0;
	position: absolute;
	top: 0;
	right: 166px;
	height: 40px;
	line-height: 40px;
	-webkit-transition: all .3s ease 0s;
	transition: all .3s ease 0s
}

.toggle_bts a {
	float: left;
	display: block;
	width: 40px;
	height: 40px;
	font-size: 29px;
	color: #000;
	background: #fff;
	text-align: center
}

.toggle_bts a .icon {
